import { useEffect, useState,useContext } from 'react';
import colaboradorServer from '../services/ColaboradorServer';

import { Navbar, Container, Button, Modal, Form} from 'react-bootstrap';
import { useForm } from "react-hook-form";

import imagens from '../user-avatar.png';


function ColaboradorComponente() {

  const { register, handleSubmit , reset } = useForm();
  const handleRegistration = (data) => {
    colaboradorServer.create(data);
    getColaborador();
    reset();

  };


  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);



  const [colaborador, setColaborador] = useState([]);
  const [count, setCount] = useState(0);

  useEffect(()=>{
    getColaborador()
  }, [])


  const getColaborador = () =>{
    colaboradorServer.getAll().then(response =>{
      console.log(response.data);
      setColaborador(response.data);
    });
  }
  
  return (
    <>
    <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="#home">Cafe da manha</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse className="justify-content-end">
          <Button variant="primary" onClick={handleShow}>Cadastro</Button>
        </Navbar.Collapse>
      </Container>
    </Navbar>

    
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>cadatro de participantes</Modal.Title>
      </Modal.Header>
      <Form  onSubmit={handleSubmit(handleRegistration)}>
        <Modal.Body>
          <Form.Group className="mb-3" controlId="formBasicNome">
            <Form.Label>Nome</Form.Label>
            <Form.Control type="text" placeholder="Enter nome" name="nome" {...register('nome', { required: true })} />
            <Form.Text className="text-muted">
            </Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicCpf">
            <Form.Label>Cpf</Form.Label>
            <Form.Control type="text" placeholder="Enter cpf" name="cpf" {...register('cpf', { required: true })} />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicAlimento">
            <Form.Label>Nome alimento</Form.Label>
            <Form.Control type="text" placeholder="Enter Alimento"  name="alimento" {...register('alimento', { required: true })} />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button   type="submit" variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
    <Container>
      <div className='jumbotron'>
        {colaborador.map(
          colaborado =>
          <div className="card mb-3 colaboradorCard" key = {colaborado.id}>
            <div className="row g-0">
              <div className="col-md-4 colaboradorCardImg" >
                <img src={imagens} className="img-fluid rounded-start colaboradorCardDivImg" />
                </div>
                <div className="col-md-8">
                  <div className="card-body">
                    <h5 className="card-title">Nome: {colaborado.nome }</h5>
                    <p className="card-text">Cpf: {colaborado.cpf }</p>
                    <p className="card-text">Comida: {colaborado.alimento }</p>
                  </div>
                </div>
              </div>
            </div>
          )
        }
      </div>
    </Container>
    </>
  );
}

export default ColaboradorComponente;