import axios from 'axios';

const URL_BASE = 'https://app-cafemanha.herokuapp.com/api/';
const COLABORADOR_REST_URL = URL_BASE+'colaborador'

class ColaboradorServer{
    getAll = () => {
        return axios.get(COLABORADOR_REST_URL);
    }
    create = obj  => {
        return axios.post(COLABORADOR_REST_URL, obj,{ }).then(({data})=> console.log(data)).catch(({data})=> console.log(data));




        //return axios.post(COLABORADOR_REST_URL);
    }
}

export default new ColaboradorServer();